
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import Tabs from '@/components/tabs/tabs';
export default {
components: {
XHeader,
InlineLoading,
Tabs
},
data() {
return {
panes: [




],
dataSeeIng: [




],
list: {},
pageIndex: 1,
pageSize: 100000,
conIndex: 0,
liIndex: -1,
gotoDetailIndex: 0,
newConIndex: 0,
firstSet: true,//设置一级top
firstTwoSet: 0,//设置二级获取top 次数
}
},
beforeRouteLeave(to, from, next) {
if (to.name == 'diseaseList') {
window.localStorage.setItem('gotoSymptomDetails', JSON.stringify({
index: this.gotoDetailIndex,
conIndex: this.newConIndex
}));
} else {
window.localStorage.removeItem('gotoSymptomDetails');
}
next();
},
mounted() {
this.$nextTick(() => {
this.getPanes();
let gotoDetail = JSON.parse(window.localStorage.getItem('gotoSymptomDetails'));
if (gotoDetail) {
this.gotoDetailIndex = gotoDetail.index;
this.newConIndex = gotoDetail.conIndex;
}
});
},
updated() {
if (this.$refs.tabasss.$refs.myBoxLi && this.firstSet) {
this.firstSet = false;
this.liIndex = 0;
this.liIndex = this.gotoDetailIndex;
this.conIndex = this.newConIndex
}
},
methods: {

getPanes() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'autodiagnosis',
'MSH.2': 'listPart',
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data != 'undefined' && data.length > 0) {

let self = this;
for (let y = 0; y < data.length; y++) {
const element = data[y];
self.panes.push({
label: element.CVALUE,
code: element.CBH,
index: y
})
}
if (this.panes.length > 0) {



this.getList(this.gotoDetailIndex);
}
}
} else {}
})
},

getList(index) {
if (!this.list[index]) {
this.$ajax.post(this.$root.host, {
'MSH.1': 'diseasepool',
'MSH.2': 'listType',
'pageIndex': this.pageIndex,
'pageSize': this.pageSize,
'cbm': this.panes[index].code,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined' && typeof data.rows != 'undefined' && data.rows.length > 0) {

let self = this;
self.list[index] = [];
var arr = [];
for (let i = 0; i < data.rows.length; i++) {
const element = data.rows[i];
var obj = {};
obj["label"] = element.CVALUE;
obj["code"] = element.CBM;
arr.push(obj);
}
self.list[index] = arr;
this.dataSeeIng = this.list[index];
if(this.firstTwoSet<2){
this.firstTwoSet++;
this.$refs.tabasss.setScrollPosition();
}
} else {
this.dataSeeIng = [];
}
})
} else {
this.dataSeeIng = this.list[index];
}
},

onLiClick(i, j, obj) {
this.gotoDetailIndex = obj.index;
this.getList(obj.index);
},

onConClick(i, j, obj) {
this.newConIndex = j;
this.$router.push({
name: 'diseaseList',
params: {
code: obj.code,
name: obj.label
}
});
},
searchbt() {
this.$router.push({
name: "diseaseListSearch",
});
}
}
}



